<template>
	<view class="pf whtl" style="background: rgba(249, 251, 254, 1);">
		<template v-if="issign">
			<view class="animated fadeIn fast pf whtl">
				<view class="cfff pr ovh">
					<image src="../../static/bg.jpg" class="pa w100" mode="widthFix"></image>
					<view class="body ovh pr z3" style="padding-bottom: 7px;">
						<view class="ovh">
							<view class="fl center">
								<image style="width: 90px;" src="../../static/logo.png" mode="widthFix"></image>
							</view>
							<view class="fr topfr">
								<view class="fr h25"></view>
								<view class="clear"></view>
								<view class="h5"></view>
								<view class="topinput ovh" @click="getUrl('../search/search',0)">
									<view class="iconfont icon-sousuo fl" style="height: 15px;"></view>
									<view class="w10 h5 fl"></view>
									<view class="fl">输入你的搜索</view>
								</view>
							</view>
						</view>
						<view class="h10"></view>
						<view class="ovh">
							<view class="fr">
								<picker mode="date" fields="month" :value="date" @change="bindDateChange">
									<view class="center">
										<view class="f25 fb">{{date}}</view>
										<view class="w10"></view>
										<view class="triangle-down"></view>
									</view>
								</picker>
							</view>
						</view>
					</view>
				</view>
				<view class="body">
					<view class="pr">
						<swiper class="swiper" :current="timeIndex" previous-margin="270rpx" next-margin="270rpx" @change="timeDataChange">
							<swiper-item v-for="(timelist,index) in timeData" :key="index">
								<view @click="onsw(index)" class="tec" :class="timeIndex==index?'on':''">
									<view class="swiperzhou">{{timelist.weekday}}</view>
									<view class="swiperr fb">{{timelist.time}}</view>
								</view>
							</swiper-item>
						</swiper>
						<view class="pa calejbl z3"></view>
						<view class="pa calejbr z3"></view>
					</view>
				</view>
				<scroll-view class="pf" scroll-y>
					<view class="body" style="padding-top: 0px;">
						<view class="list">
							
								<block v-for="(calesw,inx) in listData" :key="inx">
									<view class="pr listli shadow animated fadeIn fast" 
									 v-bind:style="{ 'background': 'url('+calesw.thumbnail.src+') no-repeat center','background-size':'cover' }">
															
										<view>
											<template v-if="Dtime<calesw.startTime&&calesw.IsMark==false">
												<view class="caleswnav pa f12 cfff shadow2 bca021b center">
													<view class="iconfont cfff icon-yuyuezhongxin center" style="height: 18px;"></view>
													<view class="w5 h5"></view>
													<view class="fb">预约</view>
												</view>
											</template>
											<template v-if="Dtime<calesw.startTime&&calesw.IsMark==true">
												<view class="caleswnav pa f12 cfff shadow2 bca021b center">
													<view class="iconfont cfff icon-yuyuezhongxin center" style="height: 18px;"></view>
													<view class="w5 h5"></view>
													<view class="fb">已预约</view>
												</view>
											</template>
											<template v-else-if="(Dtime>=calesw.startTime)&&(Dtime<=calesw.endTime)">
												<view class="caleswnav pa f12 cfff shadow2 bca021b center">
													<view class="iconfont cfff icon-yuyuezhongxin center" style="height: 18px;"></view>
													<view class="w5 h5"></view>
													<view class="fb">直播中</view>
												</view>
											</template>
											<template v-else-if="Dtime>calesw.endTime">
												<view class="caleswnav pa f12 cfff shadow bAAB2B7 center">
													<view class="iconfont cfff icon-yuyuezhongxin center" style="height: 18px;"></view>
													<view class="w5 h5"></view>
													<view class="fb">已结束</view>
												</view>
											</template>
										</view>
										
										
										<view>
											<template v-if="(Dtime>=calesw.startTime)&&(Dtime<=calesw.endTime)">
												<view class="pa whtl" @click="_getUrl(calesw.link,0)"></view>
											</template>
											<template v-else>
												<view class="pa whtl" @click="getUrl('../predetails/predetails?id='+calesw.id,0)"></view>
											</template>
										</view>
										
									</view>
								</block>
								<template v-if="isnone&&listData.length==0">
									<noNe tab="4"></noNe>
								</template>
								
								<template v-if="!isnone||listData.length!=0">
									<view>
										<uni-load-more iconType="circle" :status="status" />
										<view class="h20"></view>
									</view>
								</template>
								<boView></boView>
							
							
						</view>
					</view>
				</scroll-view>
				<foTter nav="3"></foTter>
			</view>

		</template>
	</view>
</template>

<script>
	import foTter from '@/components/footer.vue'
	import boView from '@/components/botview.vue'
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
	import uniSection from '@/components/uni-section/uni-section.vue'
	import noNe from '@/components/none.vue'
	export default {
		components: {
			foTter,
			boView,
			uniLoadMore,
			uniSection,
			noNe
		},
		data() {
			const currentDate = this.getDate({
				format: true
			})
			return {
				newTime:"",
				oldTime:"",
				issign: false,
				isnone:false,
				date: currentDate,
				serverTime:"",
				timeData: [],
				timeW:"173",
				timeIndex: 0,
				page: 0,
				Dtime:"",
				listData: [],
				status: 'more',
				isUpdata: true,
				statusTypes: [{
					value: 'more',
					text: '加载前',
					checked: true
				}, {
					value: 'loading',
					text: '加载中',
					checked: false
				}, {
					value: 'noMore',
					text: '没有更多',
					checked: false
				}]
			}
		},
		onLoad() {
			var sign = this.$conFig.issign('false',getCurrentPages()[0].route)
			if (sign) {
				this.issign=true
				//this.$conFig.alert_Load()
				this.load()
				
				this.Dtime = (new Date()).getTime();
				this.setTim()
			}
		},
		onShow() {
			this.$conFig.onWeixin("领医迈","　","/pages/index/index","s.jpg")
		},
		onShareAppMessage(res) {
			
			return {
				title: "领医迈",
				path: '/pages/index/index'
			}
		},
		methods: {
			setTim: function(e) {
				setTimeout(() => {
					this.Dtime = this.Dtime + 1000;
					this.setTim()
				}, 1000)
			},
			load() {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();
				
				this.isUpdata=true
				this.status = "loading"
				this.page = "0"
				this.updata(year, month, day)
				this.getDatenum(year, month)
			},
			_getUrl(e){
				console.log(e)
				window.location.href = this.$base64.Base64.decode(e)
			},
			getUrl(e, u) {
				if(this.$conFig.onUrl(e)){
					uni.navigateTo({
						url: e
					});
				}
			},
			onsw(i){
				this.timeIndex = i
			},
			timeDataChange: function(e) {
				let current = e.mp.detail.current;
				this.timeIndex = current;
				
				this.isUpdata=true
				this.status = "loading"
				this.page = "0"
				this.updata(this.date.split("-")[0], this.date.split("-")[1], this.timeIndex + 1)
			},
			bindDateChange: function(e) {
				this.date = e.target.value
				this.getDatenum(this.date.split("-")[0], this.date.split("-")[1])
				
				this.isUpdata=true
				this.status = "loading"
				this.page = "0"
				this.updata(this.date.split("-")[0], this.date.split("-")[1], this.timeIndex + 1)
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;

				this.getDatenum(year, month)
				return `${year}-${month}`;
			},
			getDatenum(year, month) {
				var d = new Date(year, month, 0);

				var time = []
				for (var i = 0; i < d.getDate(); i++) {
					var _i = i + 1
					time.push({
						"time": _i < 10 ? '0' + _i : _i,
						"weekday": this.timehs(year + "-" + month + "-" + _i)
					})
					
					//console.log(year + "," + month + "," + _i,this.timehs(year + "," + month + "," + _i),"/////")
				}
				
				

				const date = new Date();
				let _year = date.getFullYear();
				let _month = date.getMonth() + 1;
				let _day = date.getDate();

				if (year == _year && month == _month) {
					this.timeIndex = _day - 1
				} else {
					this.timeIndex = 0
				}
				this.timeData = time
			},
			timehs(e) {
				var mydate = new Date(e.replace(/-/g, '/'));
				var myddy = mydate.getDay(); //获取存储当前日期
				var weekday = ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"];
				
				return weekday[myddy]
			},
			updata(y, m, d) {
				
				if (!this.isUpdata || this.status == "noMore") {
					return
				}
				this.isUpdata = false
				this.status = "loading"
				this.isnone=false
				
				this.listData = []
				
				this.$reqUest.get('inmedia', '/linyimaiapi.ashx?Method=broadcast', {
					"category": uni.getStorageSync('timeDepartment').category,
					"openid": uni.getStorageSync('wxuserInfo').openid,
					"tags": "强生微课",
					"date": y+"-"+m+"-"+d,
					page:this.page
				}).then(data => {
					var [error, res] = data;
					this.isnone=true
					
					
					this.currentCaleswIndex=0
					
					
					this.issign = true
					uni.hideLoading();
					
					
					console.log(y+"-"+m+"-"+d)
					
					this.newTime=y+"-"+m+"-"+d
					
					if(this.newTime!=this.oldTime){
						this.listData = []
					}
					this.oldTime=this.newTime
				
					
					this.status = "more"
					
					if (res.data.result != "true") {
						this.status = "noMore"
						return
					}
					
					this.page++
					
					var list = this.$conFig.newList(JSON.parse(res.data.message))
					
					
					
					for (var i = 0; i < list.length; i++) {
						var etime = list[i].endTime
						var stime = list[i].startTime
						var rtime = list[i].requestTime
					
					
						list[i].endTime = etime.substring(etime.indexOf('/Date(') + 6, etime.length - 2)
						list[i].startTime = stime.substring(stime.indexOf('/Date(') + 6, stime.length - 2)
						list[i].requestTime = rtime.substring(rtime.indexOf('/Date(') + 6, rtime.length - 2)
						this.serverTime = rtime.substring(rtime.indexOf('/Date(') + 6, rtime.length - 2)
						
						list[i].link=this.$base64.Base64.encode(list[i].link)
					}
					
					this.listData = [...this.listData, ...list]
					this.isUpdata = true;
					
					if (this.$conFig.newListLength(res.data) != 21) {
						this.status = "noMore"
					}
					
					
					
				})

				
			}
		}
	}
</script>

<style>
	page {
		background: rgba(249, 251, 254, 1);
	}

	scroll-view {
		top: 218px;
		left: 0px;
		bottom: 0px;
		left: 0px;
	}

	.swiper {
		color: #3E3D3D;
		height: 57px;
	}

	.swiperzhou {}

	.swiperr {
		font-size: 22px;
	}

	.swiper .tec.on {
		color: #B92727;
	}

	.swiper .tec.on .swiperr {
		font-size: 32px;
		line-height: 1;
	}

	.calejbl,
	.calejbr {
		width: 128px;
		height: 48px;
		top: 0px;
		height: 100%;
		pointer-events: none;
	}

	.calejbl {
		left: 0px;
		background: linear-gradient(90deg, rgba(249, 251, 254, 1) 0%, rgba(249, 251, 254, 0) 100%);
	}

	.calejbr {
		right: 0px;
		background: linear-gradient(90deg, rgba(249, 251, 254, 0) 0%, rgba(249, 251, 254, 1) 100%);
	}
	
	.listli{ height: 360rpx; }
</style>
